export default "<template>\n  <div>\n    <div class=\"sliders\">\n      <p>Max height: {{ maxHeight }}px</p>\n      <input v-model.number=\"maxHeight\" type=\"range\" min=\"1\" max=\"600\" />\n    </div>\n    <fd-popover :body-styles=\"{ maxHeight: `${maxHeight}px` }\" boundary=\"viewport\">\n      <template #control=\"{toggle}\">\n        <fd-button @click=\"toggle\">Long options list</fd-button>\n      </template>\n      <template #default=\"{hide}\">\n        <fd-menu @select=\"hide\">\n          <fd-menu-list>\n            <fd-menu-item v-for=\"i in items\" :key=\"i\">Option {{ i }}</fd-menu-item>\n          </fd-menu-list>\n        </fd-menu>\n      </template>\n    </fd-popover>\n  </div>\n</template>\n\n<script>\nexport default {\n  computed: {\n    items() {\n      return this.generateData();\n    }\n  },\n  methods: {\n    generateData() {\n      const items = [];\n      for (let i = 0; i < 100; i++) {\n        items.push(i);\n      }\n      return items;\n    }\n  },\n  data() {\n    return {\n      maxHeight: 250\n    };\n  }\n};\n</script>\n\n<style>\n.sliders,\n.sliders input {\n  width: 100%;\n}\n</style>\n"