export default "<template>\n  <fd-table\n    selection-mode=\"multiple\"\n    :headers=\"['firstName', 'lastName', 'building']\"\n    :items=\"items\"\n  >\n    <template #row=\"{ item }\">\n      <fd-table-row @click=\"onRowClick(item)\">\n        <template #firstName>\n          <fd-table-cell>\n            <fd-button @click.stop=\"onButtonClick\">{{ item.firstName }}</fd-button>\n          </fd-table-cell>\n        </template>\n\n        <template #lastName>\n          <fd-table-cell>{{ item.lastName }}</fd-table-cell>\n        </template>\n\n        <template #building>\n          <fd-table-cell>{{ item.building }}</fd-table-cell>\n        </template>\n      </fd-table-row>\n    </template>\n  </fd-table>\n</template>\n\n<script>\nexport default {\n  methods: {\n    onButtonClick() {\n      console.log(\"click on button\"); // eslint-disable-line no-console\n    },\n    onRowClick(item) {\n      console.log(\"click on row representing item: %o\", item); // eslint-disable-line no-console\n    }\n  },\n  data: () => ({\n    items: [\n      {\n        id: \"1\",\n        rating: 1,\n        firstName: \"Chris\",\n        lastName: \"Kienle\",\n        building: \"WFD02\"\n      },\n      {\n        id: \"2\",\n        rating: 2,\n        firstName: \"Andi\",\n        lastName: \"Kienle\",\n        building: \"WFD03\"\n      },\n      {\n        id: \"3\",\n        rating: 3,\n        firstName: \"Sven\",\n        lastName: \"Bacia\",\n        building: \"WFD02\"\n      },\n      {\n        id: \"4\",\n        rating: 4,\n        firstName: \"Artur\",\n        lastName: \"Raess\",\n        building: \"WFD02\"\n      }\n    ]\n  })\n};\n</script>\n"