<template><div>
  <h1 class="fdd-md--h1">fd-date-input</h1>
<p class="fdd-md--p">Allows the user to enter a single date or a date range in text form.</p>
<h2 class="fdd-md--h2">Props</h2>
<h3 class="fdd-md--h3">mode</h3>
<p class="fdd-md--p">Input mode – ‘single’ allows the user to input a single date, ‘range’ allows the user to enter a date range.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>String</code></p>
<p class="fdd-md--p"><code>'range'</code> / <code>'single'</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>&quot;single&quot;</code></p>
<h3 class="fdd-md--h3">value</h3>
<p class="fdd-md--p">The value – can be a range or a single date – however in both cases an object with <code>from</code>- and <code>to</code>-properties is expected.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Object</code></p>
<p class="fdd-md--p">The value-object should have the following shape:</p>
<pre v-pre class="language-typescript"><code><span class="token punctuation">{</span> <span class="token keyword">from</span><span class="token operator">?</span><span class="token punctuation">:</span> Date<span class="token punctuation">,</span> to<span class="token operator">?</span><span class="token punctuation">:</span> Date <span class="token punctuation">}</span>
</code></pre>
<p class="fdd-md--p"><strong>Default:</strong> <code>–</code></p>
<p class="fdd-md--p">Defaults to the ‘null-date’: <code>{ from?: Date, to?: Date }</code></p>
<h3 class="fdd-md--h3">dateFormatter</h3>
<p class="fdd-md--p">Custom date formatter – should return a string for a given <code>Date</code>.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Function</code></p>
<p class="fdd-md--p"><code>date =&gt; string</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>DefaultDateFormatter</code></p>
<p class="fdd-md--p">A default date formatter provided by the framework.</p>
<h3 class="fdd-md--h3">dateRangeFormatter</h3>
<p class="fdd-md--p">Custom date range formatter.
Should return a string for a given date range:</p>
<pre v-pre class="language-typescript"><code><span class="token punctuation">{</span> <span class="token keyword">from</span><span class="token operator">?</span><span class="token punctuation">:</span> Date<span class="token punctuation">,</span> to<span class="token operator">?</span><span class="token punctuation">:</span> Date <span class="token punctuation">}</span>
</code></pre>
<p class="fdd-md--p"><strong>Type:</strong> <code>Function</code></p>
<p class="fdd-md--p"><code>({from?: Date, to?: Date}) =&gt; string</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>DefaultDateRangeFormatter</code></p>
<p class="fdd-md--p">A default date range formatter provided by the framework.</p>
<h3 class="fdd-md--h3">dateParser</h3>
<p class="fdd-md--p">Custom date parser – should return a <code>Date</code> for a given <code>String</code>.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Function</code></p>
<p class="fdd-md--p"><code>String =&gt; Date</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>DefaultDateParser</code></p>
<p class="fdd-md--p">A default date parser provided by the framework.</p>
<h3 class="fdd-md--h3">dateRangeParser</h3>
<p class="fdd-md--p">Custom date range parser – should return a date range for a given <code>String</code>.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Function</code></p>
<p class="fdd-md--p"><code>String =&gt; ({from?: Date, to?: Date})</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>DefaultDateRangeParser</code></p>
<p class="fdd-md--p">A default date range parser provided by the framework.</p>
<h2 class="fdd-md--h2">Events</h2>
<h3 class="fdd-md--h3">input</h3>
<p class="fdd-md--p">Emitted whenever the date or range has been confirmed by the user.</p>
<p class="fdd-md--p"><strong>Arguments:</strong></p>
<ul class="fdd-md--ul">
<li class="fdd-md--li">An object with two keys: <code>from</code> and <code>to</code> – both can be either <code>null</code> or <code>Date</code>. In <code>single</code>-mode only <code>from</code> will be a <code>Date</code>.</li>
</ul>

  </div></template>
