export default "<template>\n  <!--\n    You have to import the CSS that is part of vue-virtual-scroller\n    manually. Otherwise this component will not work:\n\n    import \"vue-virtual-scroller/dist/vue-virtual-scroller.css\";\n  -->\n  <fd-virtualized-list style=\"height: 400px;\" :min-item-size=\"30\" :items=\"items\" key-field=\"id\">\n    <template #item=\"{ item, index }\">\n      <fd-tile is-button transparent>\n        <fd-tile-content>\n          <fd-tile-title>#{{ index }} {{ item.title }}</fd-tile-title>\n        </fd-tile-content>\n      </fd-tile>\n    </template>\n  </fd-virtualized-list>\n</template>\n\n<script>\nexport default {\n  data() {\n    return {\n      items: [\n        { id: \"1\", title: \"Item A\" },\n        { id: \"2\", title: \"Item B\" },\n        { id: \"3\", title: \"Item C\" },\n        { id: \"4\", title: \"Item D\" },\n        { id: \"5\", title: \"Item E\" },\n        { id: \"6\", title: \"Item F\" },\n        { id: \"7\", title: \"Item G\" },\n        { id: \"8\", title: \"Item H\" },\n        { id: \"9\", title: \"Item I\" },\n        { id: \"10\", title: \"Item J\" },\n        { id: \"11\", title: \"Item K\" },\n        { id: \"12\", title: \"Item L\" }\n      ]\n    };\n  }\n};\n</script>\n"