export default "<template>\n  <fd-menu @select=\"sayHi\">\n    <fd-menu-list>\n      <fd-menu-item value=\"1\">Option 1</fd-menu-item>\n      <fd-menu-item value=\"2\">\n        Option 2\n      </fd-menu-item>\n      <fd-menu-item value=\"3\">\n        Option 3\n      </fd-menu-item>\n      <fd-menu-item value=\"4\">\n        Option 4\n      </fd-menu-item>\n    </fd-menu-list>\n  </fd-menu>\n</template>\n\n<script>\nexport default {\n  methods: {\n    sayHi(item) {\n      alert(`Clicked Menu Item with value: '${item.value}'`);\n    }\n  }\n};\n</script>\n"