export default "<template>\n  <fd-master-details\n    :master-items=\"items\"\n    :virtualized-list-props=\"optsOverrides\"\n    :use-virtualized-list=\"true\"\n    :selection-handler=\"itemSelected\"\n    style=\"max-height: 600px;\"\n  >\n    <template #top-bar>\n      <fd-action-bar>\n        <template #header>\n          <fd-action-bar-header>\n            <template #title>\n              <fd-shell-bar>\n                <fd-shell-bar-group position=\"start\">\n                  <fd-shell-bar-logo\n                    alt=\"SAP\"\n                    :src=\"$withBase('images/sap-logo.png')\"\n                    :srcset=\"srcset\"\n                    width=\"48\"\n                    height=\"24\"\n                  />\n                  <fd-shell-bar-product productTitle=\"Top bar\" />\n                </fd-shell-bar-group>\n                <fd-shell-bar-group position=\"end\">\n                  <fd-shell-bar-actions>\n                    <fd-shell-bar-action>\n                      <fd-shell-bar-user-menu ref=\"userMenu\">\n                        <fd-menu>\n                          <fd-menu-list>\n                            <fd-menu-item>Settings</fd-menu-item>\n                            <fd-menu-item>Sign out</fd-menu-item>\n                          </fd-menu-list>\n                        </fd-menu>\n                      </fd-shell-bar-user-menu>\n                    </fd-shell-bar-action>\n                  </fd-shell-bar-actions>\n                </fd-shell-bar-group>\n              </fd-shell-bar>\n            </template>\n          </fd-action-bar-header>\n        </template>\n      </fd-action-bar>\n    </template>\n    <template #master-title>\n      <fd-action-bar>\n        <template #header>\n          <fd-action-bar-header>\n            <template #title>\n              <fd-action-bar-title>Master title</fd-action-bar-title>\n            </template>\n          </fd-action-bar-header>\n        </template>\n      </fd-action-bar>\n    </template>\n    <template v-slot:master-list-item=\"{ item, index }\">\n      Item #{{ index }}\n    </template>\n    <template #details-title=\"{ item }\">\n      <fd-action-bar>\n        <template #header>\n          <fd-action-bar-header>\n            <template #title>\n              <fd-action-bar-title>Details</fd-action-bar-title>\n            </template>\n            <template #description>\n              <fd-action-bar-description>Description</fd-action-bar-description>\n            </template>\n          </fd-action-bar-header>\n        </template>\n        <template v-if=\"item\" #actions>\n          <fd-action-bar-actions>\n            <fd-button type=\"positive\" icon=\"edit\">Edit</fd-button>\n            <fd-button type=\"negative\" icon=\"delete\">Delete</fd-button>\n          </fd-action-bar-actions>\n        </template>\n      </fd-action-bar>\n    </template>\n    <template #details-content=\"{ item }\">\n      <fd-panel :title=\"`#${item.id}`\" :description=\"`Details about ${item.value}`\">\n        <p>{{ item.apiResult }}</p>\n      </fd-panel>\n    </template>\n    <template #details-content-placeholder>\n      <div class=\"empty\">\n        <div class=\"emtpty-image\">\n          <img :src=\"$withBase('images/no-data.svg')\" alt=\"details content placeholder\" />\n        </div>\n        <p>Placeholder</p>\n      </div>\n    </template>\n  </fd-master-details>\n</template>\n\n<script>\nexport default {\n  computed: {\n    srcset() {\n      return [\n        `${this.$withBase(\"images/sap-logo@2x.png\")} 2x`,\n        `${this.$withBase(\"images/sap-logo@3x.png\")} 3x`,\n        `${this.$withBase(\"images/sap-logo@4x.png\")} 4x`\n      ].join(\", \");\n    }\n  },\n  data() {\n    return {\n      items: this.makeItems(),\n      optsOverrides: {\n        loadMoreItems: done => {\n          this.items = this.items.concat(...this.makeItems(this.items[this.items.length - 1].id));\n          done();\n        },\n        totalItemCount: 500\n      }\n    };\n  },\n  methods: {\n    makeItems(firstId = 0) {\n      let items = [];\n      for (let i = 0; i < 100; i++) {\n        items.push({ id: firstId + i, value: `AC-COB${i}` });\n      }\n      return items;\n    },\n    async itemSelected(item) {\n      item.apiResult = \"I was added in the selectionHandler\";\n      // simulate api request\n      await this.timeout(800);\n      return item;\n    },\n    timeout(ms) {\n      return new Promise(resolve => setTimeout(resolve, ms));\n    }\n  }\n};\n</script>\n\n<style lang=\"scss\">\n.empty {\n  margin: 0 8px;\n  font-size: 14px;\n  line-height: 22px;\n  text-align: center;\n}\n.empty-image {\n  height: 100px;\n  margin-bottom: 8px;\n}\n\n.empty-image img {\n  height: 100%;\n  vertical-align: middle;\n  border-style: none;\n}\n\n.empty-description {\n  margin: 0;\n  padding: 0;\n}\n</style>\n"