export default "<template>\n  <div>\n    <fd-shell-bar>\n      <fd-shell-bar-group position=\"start\">\n        <fd-shell-bar-logo\n          alt=\"SAP\"\n          :src=\"$withBase('images/sap-logo.png')\"\n          :srcset=\"srcset\"\n          width=\"48\"\n          height=\"24\"\n        />\n        <fd-shell-bar-product productTitle=\"Product Name\" />\n      </fd-shell-bar-group>\n      <fd-shell-bar-group position=\"end\">\n        <fd-shell-bar-actions>\n          <fd-shell-bar-action>\n            <fd-shell-bar-action-button icon=\"sys-help\" />\n          </fd-shell-bar-action>\n          <fd-shell-bar-action>\n            <fd-shell-bar-action-button icon=\"settings\" />\n          </fd-shell-bar-action>\n          <fd-shell-bar-action>\n            <fd-shell-bar-user-menu>\n              <fd-menu>\n                <fd-menu-list>\n                  <fd-menu-item>\n                    Settings\n                  </fd-menu-item>\n                  <fd-menu-item>\n                    Sign out\n                  </fd-menu-item>\n                </fd-menu-list>\n              </fd-menu>\n            </fd-shell-bar-user-menu>\n          </fd-shell-bar-action>\n        </fd-shell-bar-actions>\n      </fd-shell-bar-group>\n    </fd-shell-bar>\n  </div>\n</template>\n\n<script>\nexport default {\n  computed: {\n    srcset() {\n      return [\n        `${this.$withBase(\"images/sap-logo@2x.png\")} 2x`,\n        `${this.$withBase(\"images/sap-logo@3x.png\")} 3x`,\n        `${this.$withBase(\"images/sap-logo@4x.png\")} 4x`\n      ].join(\", \");\n    }\n  }\n};\n</script>\n"