export default "<template>\n  <div>\n    <fd-button @click=\"visible = !visible\">Toggle Visibile</fd-button>\n\n    <br />\n    <br />\n\n    <fd-alert type=\"success\" v-model=\"visible\" dismissible>\n      Success alert. Click on the close button.\n    </fd-alert>\n  </div>\n</template>\n\n<script>\nexport default {\n  data() {\n    return {\n      visible: true\n    };\n  }\n};\n</script>\n"