export default "<template>\n  <div>\n    <fd-button :disabled=\"addDiabled\" @click=\"cols++\" styling=\"light\" icon=\"sys-add\">\n      Column\n    </fd-button>\n    <fd-button :disabled=\"removeDisabled\" @click=\"cols--\" styling=\"light\" icon=\"sys-minus\">\n      Column\n    </fd-button>\n    <hr />\n    <br />\n    <fd-container fluid>\n      <fd-col :span=\"span\" v-for=\"col in cols\" :key=\"`col-${col}`\">{{ col }}. Column</fd-col>\n    </fd-container>\n  </div>\n</template>\n\n<script>\nexport default {\n  data() {\n    return { cols: 3 };\n  },\n  computed: {\n    span() {\n      return 12 / this.cols;\n    },\n    addDiabled() {\n      return this.cols > 11;\n    },\n    removeDisabled() {\n      return this.cols < 1;\n    }\n  }\n};\n</script>\n"