export default "<template>\n  <fd-table striped :headers=\"['firstName', 'lastName', 'building']\" :items=\"items\">\n    <template #row=\"{ item }\">\n      <fd-table-row>\n        <template #firstName>\n          <fd-table-cell>\n            {{ item.firstName }}\n          </fd-table-cell>\n        </template>\n        <template #lastName>\n          <fd-table-cell>\n            {{ item.lastName }}\n          </fd-table-cell>\n        </template>\n        <template #building>\n          <fd-table-cell>\n            {{ item.building }}\n          </fd-table-cell>\n        </template>\n      </fd-table-row>\n    </template>\n  </fd-table>\n</template>\n\n<script>\nexport default {\n  data: () => ({\n    items: [\n      { id: \"1\", firstName: \"Chris\", lastName: \"Kienle\", building: \"WFD02\" },\n      { id: \"2\", firstName: \"Andi\", lastName: \"Kienle\", building: \"WFD03\" },\n      { id: \"3\", firstName: \"Sven\", lastName: \"Bacia\", building: \"WFD02\" },\n      { id: \"4\", firstName: \"Artur\", lastName: \"Raess\", building: \"WFD02\" }\n    ]\n  })\n};\n</script>\n"