export default {"componentDesc":{"default":["Allows the user to enter a single date or a date range in text form."]},"name":"fd-date-input","events":[{"name":"input","isSync":false,"syncProp":"","describe":["Emitted whenever the date or range has been confirmed by the user."],"argumentsDesc":["An object with two keys: `from` and `to` – both can be either `null` or `Date`. In `single`-mode only `from` will be a `Date`."]}],"props":[{"name":"mode","type":"String","describe":["Input mode – 'single' allows the user to input a single date, 'range' allows the user to enter a date range."],"typeDesc":["`'range'` / `'single'`"],"validator":"value => [\"range\", \"single\"].indexOf(value) >= 0","default":"\"single\""},{"name":"value","type":"Object","describe":["The value – can be a range or a single date – however in both cases an object with `from`- and `to`-properties is expected."],"typeDesc":["The value-object should have the following shape:","```typescript","{ from?: Date, to?: Date }","```"],"defaultDesc":["Defaults to the 'null-date': `{ from?: Date, to?: Date }`"]},{"name":"dateFormatter","type":"Function","describe":["Custom date formatter – should return a string for a given `Date`."],"typeDesc":["`date => string`"],"default":"DefaultDateFormatter","defaultDesc":["A default date formatter provided by the framework."]},{"name":"dateRangeFormatter","type":"Function","describe":["Custom date range formatter.","Should return a string for a given date range:","```typescript","{ from?: Date, to?: Date }","```"],"typeDesc":["`({from?: Date, to?: Date}) => string`"],"default":"DefaultDateRangeFormatter","defaultDesc":["A default date range formatter provided by the framework."]},{"name":"dateParser","type":"Function","describe":["Custom date parser – should return a `Date` for a given `String`."],"typeDesc":["`String => Date`"],"default":"DefaultDateParser","defaultDesc":["A default date parser provided by the framework."]},{"name":"dateRangeParser","type":"Function","de