export default "<template>\n  <div>\n    <div style=\"display:flex;\">\n      <fd-calendar mode=\"range\" v-model=\"date\" />\n      <fd-calendar mode=\"range\" v-model=\"date\" />\n    </div>\n    <p>\n      <strong>From:</strong>\n      {{ date.from }}\n    </p>\n    <p>\n      <strong>To:</strong>\n      {{ date.to }}\n    </p>\n  </div>\n</template>\n\n<script>\nexport default {\n  data: () => ({\n    date: {\n      from: null,\n      to: null\n    }\n  })\n};\n</script>\n"