  <h1 class="fdd-md--h1">fd-tile</h1>
<p class="fdd-md--p">A tile component can be used to display information in a simple container format. This component is ideal for displaying collection data when a grid or list layout is preferred. See <d-component-api-link type="prominent" component-name="fd-tile-grid"></d-component-api-link> and <d-component-api-link type="prominent" component-name="fd-virtualized-list"></d-component-api-link>.</p>
