<h3 class="fdd-md--h3">transparent</h3>
<p class="fdd-md--p">If true, the file has no background color – it becomes transparent. This is used in combination with fd-virtualized-list in order to render rows. Since this prop relies on custom styles you have to include the Fundamental Vue supporting styles.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Boolean</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>false</code></p>
<h3 class="fdd-md--h3">isButton</h3>
<p class="fdd-md--p">If true, the tile behaves like a button and emits <code>click</code> is not disabled.</p>
