<p class="fdd-md--p"><strong>Default:</strong> <code>false</code></p>
<h3 class="fdd-md--h3">disabled</h3>
<p class="fdd-md--p">Disables the tile.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Boolean</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>false</code></p>
<h3 class="fdd-md--h3">rowSpan</h3>
<p class="fdd-md--p">Number of rows this tile spans when used inside <d-component-api-link type="prominent" component-name="fd-tile-grid"></d-component-api-link></p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Number</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>null</code></p>
<h3 class="fdd-md--h3">colSpan</h3>
<p class="fdd-md--p">Number of columns this tile spans when used inside <d-component-api-link type="prominent" component-name="fd-tile-grid"></d-component-api-link></p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Number</code></p>
