export default {"componentDesc":{"default":["The popover is a wrapping component that renders a control and can display a popover that is attached to the control."]},"name":"fd-popover","mixIns":[{"mixIn":"Uid"}],"props":[{"name":"adjustsBodyWidth","type":"Boolean","describe":[],"default":"false"},{"name":"bodySizeMode","type":"String","describe":["Use a custom body size mode. For details please refer to the [K-Pop](https://christiankienle.github.io/k-pop/examples/#body-size-modes) documentation."],"typeDesc":["Possible Values: `auto` / `equal-trigger` / `at-least-trigger`"],"validator":"BodySizeMode.isValid","default":"BodySizeMode.defaultMode","defaultDesc":["`auto`"]},{"name":"bodyStyles","type":"Object","describe":["Custom styles object"]},{"name":"defaultBodyZIndex","type":"Number","describe":["Default z-index that should be used for the popover body."],"default":"1000","defaultDesc":["`1000`"]},{"name":"ignoredElements","type":"Function","describe":["Customize elements that can be interacted with, without the popover being dismissed."],"typeDesc":["A function that returns an array of `Element`s that should be ignored:","Signature: `() => Element[]`"],"default":"() => []","defaultDesc":["By default no elements are ignored: `() => []`"]},{"name":"withArrow","type":"Boolean","describe":["Whether or not the popover body has an arrow pointing to the control."],"default":"false","defaultDesc":["`false` – no arrow is created by default"]},{"name":"placement","type":"String","describe":["Set the placement of the popover body. This is passed – as is – to Popper.js. This means that all [Popper.js placements](https://popper.js.org/popper-documentation.html#Popper.placements) can be used."],"default":"\"bottom\"","defaultDesc":["`bottom`"]}],"slots":[]};