export default "<template>\n  <fd-master-details :master-items=\"items\" style=\"max-height: 300px;\">\n    <template v-slot:details-content=\"{ item }\">\n      <fd-panel :title=\"`#${item}`\" :description=\"`Details about ${item}`\"></fd-panel>\n    </template>\n  </fd-master-details>\n</template>\n\n<script>\nexport default {\n  data() {\n    return {\n      items: this.makeItems()\n    };\n  },\n  methods: {\n    makeItems() {\n      let items = [];\n      for (let i = 0; i < 100; i++) {\n        items.push(`AC-COB${i}`);\n      }\n      return items;\n    }\n  }\n};\n</script>\n"