export default "<template>\n  <div>\n    <fd-table :headers=\"headers\" :items=\"items\">\n      <template #row=\"{ item }\">\n        <fd-table-row>\n          <template #rating>\n            <fd-table-cell>\n              {{ item.rating }}\n            </fd-table-cell>\n          </template>\n          <template #firstName>\n            <fd-table-cell>\n              {{ item.firstName }}\n            </fd-table-cell>\n          </template>\n          <template #lastName>\n            <fd-table-cell>\n              {{ item.lastName }}\n            </fd-table-cell>\n          </template>\n          <template #fullName>\n            <fd-table-cell>{{ item.firstName }} {{ item.lastName }}</fd-table-cell>\n          </template>\n        </fd-table-row>\n      </template>\n    </fd-table>\n\n    <h2>Creating an Entry</h2>\n\n    <fd-field-set>\n      <fd-form-item label=\"First Name\">\n        <fd-input v-model=\"newEntry.firstName\" placeholder=\"Enter something nice\" />\n      </fd-form-item>\n      <fd-form-item label=\"Last Name\">\n        <fd-input v-model=\"newEntry.lastName\" placeholder=\"Enter something nice\" />\n      </fd-form-item>\n      <fd-form-item label=\"Rating\">\n        <fd-combobox v-model=\"newEntry.rating\">\n          <fd-menu-item value=\"️️️⭐️\">⭐️</fd-menu-item>\n          <fd-menu-item value=\"⭐️⭐️\">⭐️⭐️</fd-menu-item>\n          <fd-menu-item value=\"⭐️⭐️⭐️\">⭐️⭐️⭐️</fd-menu-item>\n          <fd-menu-item value=\"⭐️⭐️⭐️⭐️\">⭐️⭐️⭐️⭐️</fd-menu-item>\n          <fd-menu-item value=\"⭐️⭐️⭐️⭐️⭐️\">⭐️⭐️⭐️⭐️⭐️</fd-menu-item>\n        </fd-combobox>\n      </fd-form-item>\n    </fd-field-set>\n    <fd-button @click=\"addCurrentEntry\" icon=\"add\">Add Entry</fd-button>\n  </div>\n</template>\n\n<script>\nexport default {\n  methods: {\n    addCurrentEntry() {\n      const entry = {\n        ...this.newEntry\n      };\n      this.items = [...this.items, entry];\n      this.newEntry = {\n        firstName: null,\n        lastName: null,\n        rating: \"⭐️⭐️⭐️\",\n        id: String(this.items.length + 1)\n      };\n    }\n  },\n  data() {\n    return {\n      headers: [\n        { id: \"rating\", label: \"Rating\", sortable: true, sortBy: \"rating\" },\n        {\n          id: \"firstName\",\n          label: \"First Name\",\n          sortable: true,\n          sortBy: \"firstName\"\n        },\n        {\n          id: \"lastName\",\n          label: \"Last Name\",\n          sortable: true,\n          sortBy: \"lastName\"\n        },\n        {\n          id: \"fullName\",\n          label: \"Full Name\",\n          sortable: true,\n          sortBy: \"firstName\"\n        }\n      ],\n      newEntry: {\n        firstName: null,\n        lastName: null,\n        rating: \"⭐️⭐️⭐️\",\n        id: \"5\"\n      },\n      items: [\n        {\n          id: \"1\",\n          rating: \"⭐️⭐️⭐️\",\n          firstName: \"Chris\",\n          lastName: \"Kienle\"\n        },\n        {\n          id: \"2\",\n          rating: \"⭐️⭐️⭐️⭐️\",\n          firstName: \"Andi\",\n          lastName: \"Kienle\"\n        },\n        {\n          id: \"3\",\n          rating: \"⭐️⭐️⭐️⭐️⭐️\",\n          firstName: \"Sven\",\n          lastName: \"Bacia\"\n        },\n        {\n          id: \"4\",\n          rating: \"⭐️⭐️⭐️⭐️⭐️\",\n          firstName: \"Artur\",\n          lastName: \"Raess\"\n        }\n      ]\n    };\n  }\n};\n</script>\n"