export default "<template>\n  <fd-table\n    selection-mode=\"multiple\"\n    :headers=\"['firstName', 'lastName', 'building']\"\n    :items=\"items\"\n  >\n    <template #row=\"{ toggle, item }\">\n      <fd-table-row @click=\"toggle\">\n        <template #firstName>\n          <fd-table-cell>\n            {{ item.firstName }}\n          </fd-table-cell>\n        </template>\n        <template #lastName>\n          <fd-table-cell>\n            {{ item.lastName }}\n          </fd-table-cell>\n        </template>\n        <template #building>\n          <fd-table-cell>\n            {{ item.building }}\n          </fd-table-cell>\n        </template>\n      </fd-table-row>\n    </template>\n  </fd-table>\n</template>\n\n<script>\nexport default {\n  data: () => ({\n    items: [\n      { firstName: \"Chris\", lastName: \"Kienle\", building: \"WFD02\" },\n      { firstName: \"Andi\", lastName: \"Kienle\", building: \"WFD03\" },\n      { firstName: \"Sven\", lastName: \"Bacia\", building: \"WFD02\" },\n      { firstName: \"Artur\", lastName: \"Raess\", building: \"WFD02\" }\n    ]\n  })\n};\n</script>\n"