export default "<template>\n  <div>\n    <fd-field-set>\n      <fd-legend>Extra Small Toggles</fd-legend>\n      <fd-form-group inline>\n        <fd-form-item-toggle label=\"on\">\n          <fd-toggle on size=\"xs\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off\">\n          <fd-toggle size=\"xs\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"on + disabled\">\n          <fd-toggle disabled on size=\"xs\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off + disabled\">\n          <fd-toggle disabled size=\"xs\" />\n        </fd-form-item-toggle>\n      </fd-form-group>\n    </fd-field-set>\n\n    <fd-field-set>\n      <fd-legend>Small Toggles</fd-legend>\n      <fd-form-group inline>\n        <fd-form-item-toggle label=\"on\">\n          <fd-toggle on size=\"s\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off\">\n          <fd-toggle size=\"s\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"on + disabled\">\n          <fd-toggle disabled on size=\"s\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off + disabled\">\n          <fd-toggle disabled size=\"s\" />\n        </fd-form-item-toggle>\n      </fd-form-group>\n    </fd-field-set>\n\n    <fd-field-set>\n      <fd-legend>Large Toggles</fd-legend>\n      <fd-form-group inline>\n        <fd-form-item-toggle label=\"on\">\n          <fd-toggle on size=\"l\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off\">\n          <fd-toggle size=\"l\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"on + disabled\">\n          <fd-toggle disabled on size=\"l\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off + disabled\">\n          <fd-toggle disabled size=\"l\" />\n        </fd-form-item-toggle>\n      </fd-form-group>\n    </fd-field-set>\n  </div>\n</template>\n"