export default {"componentDesc":{"default":["A tile component can be used to display information in a simple container format. This component is ideal for displaying collection data when a grid or list layout is preferred. See `fd-tile-grid` and `fd-virtualized-list`."]},"name":"fd-tile","props":[{"name":"transparent","type":"Boolean","describe":["If true, the file has no background color – it becomes transparent. This is used in combination with fd-virtualized-list in order to render rows. Since this prop relies on custom styles you have to include the Fundamental Vue supporting styles."],"default":"false"},{"name":"isButton","type":"Boolean","describe":["If true, the tile behaves like a button and emits `click` is not disabled."],"default":"false"},{"name":"disabled","type":"Boolean","describe":["Disables the tile."],"default":"false"},{"name":"rowSpan","type":"Number","describe":["Number of rows this tile spans when used inside `fd-tile-grid`"],"default":"null"},{"name":"colSpan","type":"Number","describe":["Number of columns this tile spans when used inside `fd-tile-grid`"],"default":"null"}],"events":[{"name":"click","isSync":false,"syncProp":"","describe":["Fired only for button-tiles (`is-button` must be `true`)."]}],"slots":[{"name":"default","describe":"","backerDesc":"","bindings":{},"scoped":false,"target":"template"}]};