<p class="fdd-md--p"><code>[]</code> (empty array)</p>
<h3 class="fdd-md--h3">firstColumnFixed</h3>
<p class="fdd-md--p"><strong>Type:</strong> <code>Boolean</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>false</code></p>
<h3 class="fdd-md--h3">borderless</h3>
<p class="fdd-md--p">If <code>true</code> no border is drawn around the table.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Boolean</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>false</code></p>
<h3 class="fdd-md--h3">striped</h3>
<p class="fdd-md--p">If <code>true</code> tables are drawn with an alternating background color.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Boolean</code></p>
<p class="fdd-md--p"><strong>Default:</strong> <code>false</code></p>
<h3 class="fdd-md--h3">items</h3>
<p class="fdd-md--p">Items to be displayed in the table.</p>
<p class="fdd-md--p"><strong>Type:</strong> <code>Array</code></p>
<p class="fdd-md--p"><strong>Default:</strong> </p>
<p class="fdd-md--p"><code>[]</code> (empty array)</p>
<h3 class="fdd-md--h3">selectedIds</h3>
