<p class="fdd-md--p"><strong>Default:</strong> </p>
<p class="fdd-md--p"><code>[]</code> (empty array)</p>
<h3 class="fdd-md--h3">selectionMode</h3>
<p class="fdd-md--p">Selection mode</p>
