export default "<template>\n  <div>\n    <fd-table-fixed-wrapper style=\"width: 100%;\">\n      <fd-table-fixed>\n        <fd-table\n          :headers=\"[\n            'firstName',\n            'lastName',\n            'building',\n            'building1',\n            'building2',\n            'building3',\n            'building4',\n            'building5',\n            'building6',\n            'building7',\n            'building8',\n            'building9'\n          ]\"\n          firstColumnFixed\n          :items=\"items\"\n        >\n          <template #row=\"{ item }\">\n            <fd-table-row>\n              <template #firstName>\n                <fd-table-cell>{{ item.firstName }}</fd-table-cell>\n              </template>\n\n              <template #lastName>\n                <fd-table-cell>{{ item.lastName }}</fd-table-cell>\n              </template>\n\n              <template #building>\n                <fd-table-cell>{{ item.building }}</fd-table-cell>\n              </template>\n\n              <template #building1>\n                <fd-table-cell>{{ item.building }}1</fd-table-cell>\n              </template>\n\n              <template #building2>\n                <fd-table-cell>{{ item.building }}2</fd-table-cell>\n              </template>\n\n              <template #building3>\n                <fd-table-cell>{{ item.building }}3</fd-table-cell>\n              </template>\n\n              <template #building4>\n                <fd-table-cell>{{ item.building }}4</fd-table-cell>\n              </template>\n\n              <template #building5>\n                <fd-table-cell>{{ item.building }}5</fd-table-cell>\n              </template>\n\n              <template #building6>\n                <fd-table-cell>{{ item.building }}6</fd-table-cell>\n              </template>\n\n              <template #building7>\n                <fd-table-cell>{{ item.building }}7</fd-table-cell>\n              </template>\n\n              <template #building8>\n                <fd-table-cell>{{ item.building }}8</fd-table-cell>\n              </template>\n\n              <template #building9>\n                <fd-table-cell>{{ item.building }}9</fd-table-cell>\n              </template>\n            </fd-table-row>\n          </template>\n        </fd-table>\n      </fd-table-fixed>\n    </fd-table-fixed-wrapper>\n  </div>\n</template>\n\n<script>\nexport default {\n  data: () => ({\n    items: [\n      {\n        id: \"1\",\n        rating: 1,\n        firstName: \"Chris\",\n        lastName: \"Kienle\",\n        building: \"WFD02\"\n      },\n      {\n        id: \"2\",\n        rating: 2,\n        firstName: \"Andi\",\n        lastName: \"Kienle\",\n        building: \"WFD03\"\n      },\n      {\n        id: \"3\",\n        rating: 3,\n        firstName: \"Sven\",\n        lastName: \"Bacia\",\n        building: \"WFD02\"\n      },\n      {\n        id: \"4\",\n        rating: 4,\n        firstName: \"Artur\",\n        lastName: \"Raess\",\n        building: \"WFD02\"\n      }\n    ]\n  })\n};\n</script>\n"