export default {"componentDesc":{"default":["A table component used to display tabluar data."]},"name":"fd-table","events":[{"name":"update:selectedIds","isSync":true,"syncProp":"selectedIds","describe":[]}],"props":[{"name":"headers","type":"Array","describe":["Headers of the table"],"typeDesc":["Either an array with plain strings. For each string in the headers-array you have to provide a scoped-slot of the same name. You can also pass in header objects instead of strings to customize the headers. Refer to the table guide for more details."],"default":[],"defaultDesc":["`[]` (empty array)"]},{"name":"firstColumnFixed","type":"Boolean","describe":[],"default":"false"},{"name":"borderless","type":"Boolean","describe":["If `true` no border is drawn around the table."],"default":"false"},{"name":"striped","type":"Boolean","describe":["If `true` tables are drawn with an alternating background color."],"default":"false"},{"name":"items","type":"Array","describe":["Items to be displayed in the table."],"default":[],"defaultDesc":["`[]` (empty array)"]},{"name":"selectedIds","type":"Array","describe":["Ids of selected items."],"default":[],"defaultDesc":["`[]` (empty array)"]},{"name":"selectionMode","type":"String","describe":["Selection mode"],"typeDesc":["- `none`: no selection possible","- `single`: single row can be selected","- `multiple`: multiple rows can be selected"],"validator":"SelectionModeValidator","default":"SelectionMode.none","defaultDesc":["`none` – selecting rows is not possible by default"]}],"slots":[{"name":"row","describe":"","backerDesc":"","bindings":{"setSelected":"$_rowPropsSetSelected(item)","toggle":"$_rowPropsToggle(item)","item":"item","selectedIds":"computedSelectedIds","selected":"isSelected(item.id)","canSelect":"canSelect"},"scoped":true,"target":"template"}]};