export default "<template>\n  <div>\n    <fd-button @click=\"addColumn\">add column</fd-button>\n    <fd-table :headers=\"headers\" :items=\"items\">\n      <template #row=\"{ item }\">\n        <fd-table-row>\n          <template v-for=\"header in headers\" v-slot:[header]>\n            <fd-table-cell :key=\"item.id + header\">\n              {{ header }}\n            </fd-table-cell>\n          </template>\n        </fd-table-row>\n      </template>\n    </fd-table>\n    <fd-table :headers=\"headers\" :items=\"items\">\n      <template #row=\"{ item }\">\n        <fd-table-row :key=\"'row__item-id--' + item.id\">\n          <template v-for=\"header in headers\" v-slot:[header]>\n            <fd-table-cell :key=\"'row__item-id--' + item.id + '__for-header--' + header\">\n              {{ header }}\n            </fd-table-cell>\n          </template>\n        </fd-table-row>\n      </template>\n    </fd-table>\n  </div>\n</template>\n\n<script>\nexport default {\n  methods: {\n    addColumn() {\n      const colName = `h${this.headers.length + 1}`;\n      this.headers = [colName, ...this.headers];\n    }\n  },\n  data: () => ({\n    headers: [\"h3\", \"h2\", \"h1\"],\n    items: [\n      {\n        id: \"1\",\n        rating: 1,\n        firstName: \"Chris\",\n        lastName: \"Kienle\",\n        building: \"WFD02\"\n      },\n      {\n        id: \"2\",\n        rating: 2,\n        firstName: \"Andi\",\n        lastName: \"Kienle\",\n        building: \"WFD03\"\n      },\n      {\n        id: \"3\",\n        rating: 3,\n        firstName: \"Sven\",\n        lastName: \"Bacia\",\n        building: \"WFD02\"\n      },\n      {\n        id: \"4\",\n        rating: 4,\n        firstName: \"Artur\",\n        lastName: \"Raess\",\n        building: \"WFD02\"\n      }\n    ]\n  })\n};\n</script>\n"