export default "<template>\n  <div>\n    <fd-time\n      v-for=\"item in timeitems\"\n      :key=\"item.type\"\n      :type=\"item.type\"\n      :value=\"item.value\"\n    ></fd-time>\n  </div>\n</template>\n\n<script>\nexport default {\n  data() {\n    return {\n      timeitems: [\n        {\n          type: \"hour12\",\n          value: \"10\"\n        },\n        {\n          type: \"minute\",\n          value: \"30\"\n        },\n        {\n          type: \"second\",\n          value: \"00\"\n        },\n        {\n          type: \"meridian\",\n          value: \"am\"\n        }\n      ]\n    };\n  }\n};\n</script>\n"